html,
body {
  width: 100%;
  min-height: 100%;
  height: 100%; }

body {
  min-width: 320px;
  line-height: 1;
  color: #9eaacc;
  background-size: cover; }
  @media only screen and (max-width: 992pxpx) {
    body {
      padding-top: 70px; } }

h1, h2, h3, h4, h5, h6 {
  display: inline-block;
  font-weight: bold; }

h1,
h2 {
  margin: 56px 0 20px;
  font-size: 56px;
  line-height: 56px;
  font-weight: 300;
  color: white; }
  @media only screen and (max-width: 992pxpx) {
    h1,
    h2 {
      font-size: 46px; } }
  @media only screen and (max-width: 768pxpx) {
    h1,
    h2 {
      font-size: 30px; } }

h3 {
  margin: 20px 0;
  font-size: 18px;
  font-weight: 400;
  color: white; }

p {
  margin: 4px 0;
  line-height: 2.5;
  letter-spacing: 0.7px;
  font-size: 15px; }

a {
  display: inline-block;
  text-decoration: none; }

ul {
  padding: 0;
  margin: 0;
  list-style: none; }

img {
  display: inline-block;
  max-width: 100%; }

* {
  box-sizing: border-box; }

.App {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  background: #141e3d url("/static/images/bg-coinswap.png") repeat;
  color: #9eaacc;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.text-blue {
  color: #1796ff; }

.copyright {
  font-size: 11px; }

.button {
  padding: 2px;
  display: inline-block;
  width: 154px;
  height: 50px;
  line-height: 46px;
  border-radius: 10px;
  text-decoration: none;
  color: white;
  font-size: 13px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;
  background-image: linear-gradient(to right, #359ef6, #0662df);
  box-sizing: border-box;
  position: relative;
  border: none; }

.button.blue {
  background-image: linear-gradient(to bottom, #359ef6, #0662df);
  border: none; }

.button.blue::after {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  background-image: linear-gradient(to bottom, #0662df, #359ef6);
  transition: all 0.2s ease-in-out;
  z-index: 0;
  opacity: 0; }

.button.blue:hover::after {
  opacity: 1; }

.button.blue .button-text {
  background-color: transparent; }

.button .icon.download {
  margin-right: 8px; }

.button .icon.button-triangle {
  margin-left: 8px; }

.button span {
  z-index: 1; }

.button-text {
  display: block;
  text-align: center;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  background-color: #293056;
  position: relative;
  z-index: 1;
  transition: all 0.2s ease-in-out; }

.button:hover .button-text {
  background-color: transparent; }

.button.disabled {
  opacity: 0.2;
  pointer-events: none;
  user-select: none; }

.countdown {
  margin-top: 100px; }

.countdown-title {
  margin-bottom: 50px;
  display: block;
  text-transform: uppercase; }

.countdown-time {
  display: inline-flex;
  flex-direction: row; }

.countdown-section {
  margin: 0 10px; }

.countdown-section-title {
  margin-top: 26px;
  display: block;
  font-size: 13px;
  letter-spacing: 1px;
  text-transform: uppercase; }

.countdown-item {
  margin: 0 4px;
  display: inline-block;
  width: 54px;
  height: 72px;
  line-height: 72px;
  font-size: 22px;
  color: #4083ec;
  background-image: linear-gradient(to bottom, #1d2d62, #17234a);
  border-radius: 6px; }

.drop-down {
  display: inline-block;
  position: relative;
  padding-bottom: 10px;
  margin-bottom: -10px; }

.drop-down-menu {
  width: 100%;
  height: auto;
  position: absolute;
  left: 0;
  bottom: 0;
  transform: translateY(100%);
  background-color: #19347a;
  border: 2px solid white;
  border-radius: 10px;
  overflow: hidden;
  box-sizing: border-box;
  text-align: left;
  opacity: 0;
  transition: all 0.2s ease-in-out; }

.drop-down-menu.visible {
  opacity: 1;
  bottom: 4px; }

.drop-down-link {
  display: block;
  height: 50px;
  line-height: 50px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  font-size: 13px;
  font-weight: 700;
  color: #9eaacc;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
  box-sizing: border-box; }

.drop-down-link:last-child {
  border-bottom: none; }

.drop-down-link:hover {
  background-color: #1796ff;
  color: white; }

.icon {
  position: relative;
  display: inline-block;
  background-size: cover;
  background-position: center; }
  .icon.step-one {
    width: 40px;
    height: 40px;
    background-image: url("/static/images/step-one.svg"); }
  .icon.step-two {
    width: 40px;
    height: 40px;
    background-image: url("/static/images/step-two.svg"); }
  .icon.step-three {
    width: 40px;
    height: 40px;
    background-image: url("/static/images/step-three.svg"); }
  .icon.step-four {
    width: 40px;
    height: 40px;
    background-image: url("/static/images/step-four.svg"); }
  .icon.step-active {
    width: 27px;
    height: 11px;
    background-image: url("/static/images/icon-arrow.svg"); }
  .icon.discord {
    width: 20px;
    height: 20px;
    background-image: url("/static/images/icon-discord.svg"); }
  .icon.telegram {
    width: 20px;
    height: 20px;
    background-image: url("/static/images/icon-telegram.svg"); }
  .icon.medium {
    width: 20px;
    height: 20px;
    background-image: url("/static/images/icon-medium.svg"); }
  .icon.twitter {
    width: 20px;
    height: 20px;
    background-image: url("/static/images/icon-twitter.svg"); }

.header {
  padding: 50px 20px; }
  @media only screen and (max-width: 992pxpx) {
    .header {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      padding: 10px 20px;
      justify-content: center;
      background-color: rgba(20, 30, 61, 0.9);
      box-shadow: 10px 0 50px 0 rgba(0, 0, 0, 0.75);
      z-index: 1000; } }
  .header .language {
    margin-right: 20px; }
  .header__wrap {
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center; }
  .header__center {
    height: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
    @media only screen and (max-width: 992pxpx) {
      .header__center {
        display: none; } }
  .header__left {
    position: relative; }
  .header__right {
    display: flex;
    flex-direction: row;
    align-items: center; }
    @media only screen and (max-width: 992pxpx) {
      .header__right {
        display: none; } }

.footer {
  padding: 50px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  text-align: center;
  height: 340px;
  background-image: linear-gradient(to bottom, #1c2c66, #192859); }

.section {
  margin: 100px 0; }
  .section.hero {
    text-align: center; }
    .section.hero .buttons {
      margin-top: 10px; }
    .section.hero .hero-button {
      margin: 0 10px; }
    .section.hero .hero-link {
      color: white; }
  .section.visualization .titles {
    display: flex;
    flex-direction: row;
    justify-content: space-between; }
  .section.visualization .left {
    text-align: left; }
  .section.visualization .right {
    text-align: right; }
  .section.visualization .count {
    font-size: 20px; }
  .section.visualization .periods {
    display: flex;
    flex-direction: row;
    justify-content: space-between; }
  .section.visualization .period__num {
    color: white; }
  .section.visualization .period.current .period__data {
    color: #ffe171; }
  .section.visualization .period.next {
    text-align: right; }
    .section.visualization .period.next .period__data {
      color: #ffa46b; }
  .section.visualization .chart {
    margin-top: 40px; }
    .section.visualization .chart__img {
      width: 100%; }
  .section.how {
    text-align: center; }
    .section.how .section-right {
      margin-top: 30px; }
  .section.faq {
    text-align: center; }

.container {
  width: 1140px;
  margin: 0 auto; }

.logo {
  position: relative;
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  text-decoration: none; }
  .logo__image {
    width: 50px;
    height: 50px;
    transition: transform 1s ease-in-out; }
  .logo__text {
    margin-left: 14px;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 12px;
    letter-spacing: 1px;
    color: #dee0ee; }
    @media only screen and (max-width: 992pxpx) {
      .logo__text {
        display: none; } }

.logo:hover .logo__image {
  transform: rotateY(180deg); }

.navigation {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center; }
  .navigation__link {
    position: relative;
    margin-right: 28px;
    color: #dee0ee;
    font-size: 13px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1px; }
  .navigation__link:last-child {
    margin-right: 0; }
  .navigation__link::after, .navigation__link::before {
    position: absolute;
    content: '';
    left: 50%;
    transform: translateX(-50%);
    height: 2px;
    width: 0;
    transition: all 0.2s ease-in-out;
    background-image: linear-gradient(to right, #359ef6, #025cdd); }
  .navigation__link:after {
    bottom: -6px; }
  .navigation__link:before {
    opacity: 0.4;
    bottom: -8px; }
  .navigation__link:hover::after {
    width: 100%; }
  .navigation__link:hover::before {
    width: 80%; }
  .navigation__link.disabled {
    opacity: 0.2;
    pointer-events: none;
    user-select: none; }

.questions {
  margin: 30px auto;
  display: flex;
  flex-direction: column;
  width: 100%; }
  .questions .question {
    padding: 24px;
    width: 100%;
    border-radius: 20px;
    background: none;
    box-sizing: border-box;
    cursor: pointer;
    text-align: left; }
    .questions .question__header {
      display: flex;
      flex-direction: row;
      align-items: center; }
    .questions .question__icon {
      margin-right: 10px;
      display: inline-block;
      width: 70px;
      height: 70px;
      background-image: url("/static/images/icon-whatis.svg"); }
    .questions .question__title {
      color: white;
      font-size: 16px; }
    .questions .question__description {
      display: none;
      margin-top: 10px;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 0.8px; }
    .questions .question.active {
      background: linear-gradient(to right, #16234d, #1d2e6a);
      box-shadow: 0 20px 100px -20px rgba(0, 0, 0, 0.75); }
      .questions .question.active .question__description {
        display: block; }

.social {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center; }
  .social__el {
    margin-left: 14px;
    transition: all 0.2s ease-in-out; }
  .social__el:hover {
    transform: rotate(-15deg); }
  .social__el:first-child {
    margin-left: 0; }
  .social__el, .social__link {
    line-height: 0; }

.steps {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.step {
  position: relative;
  padding: 30px;
  width: 270px;
  height: 350px;
  display: inline-block;
  border-radius: 20px;
  background-image: linear-gradient(to right, #16234d, #1d2e6a);
  z-index: 10;
  text-align: left; }

.step-title {
  position: relative;
  margin-bottom: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
  color: white; }

.step-title-active {
  padding: 14px 0 14px 14px;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  transition: all 0.5s ease-in-out;
  cursor: pointer; }

.step-title-active:hover {
  right: -10px; }

.step-title-text {
  margin-left: 14px;
  font-weight: 600;
  text-transform: uppercase; }

.step-subtitle {
  margin: 0 0 10px;
  font-size: 15px;
  line-height: 1.6;
  text-transform: uppercase;
  color: #dee0ee; }

.step-description {
  margin: 0;
  font-size: 14px;
  line-height: 1.6; }

.step-button {
  position: absolute;
  bottom: 30px;
  left: 30px;
  width: calc(100% - 60px); }

.title {
  display: inline-block;
  position: relative;
  white-space: nowrap; }
  .title__shadow {
    position: absolute;
    bottom: 30px;
    font-size: 144px;
    font-weight: 900;
    opacity: 0.03;
    text-transform: uppercase; }
    .title__shadow.left {
      left: 0; }
    .title__shadow.right {
      right: 0; }
    @media only screen and (max-width: 992pxpx) {
      .title__shadow {
        font-size: 100px;
        bottom: 20px; } }
    @media only screen and (max-width: 768pxpx) {
      .title__shadow {
        font-size: 70px;
        bottom: 14px; } }
  .title.centred .title__shadow {
    left: 50%;
    transform: translateX(-50%); }
  .title .blue {
    color: #1796ff; }
  .title.small .title__word {
    font-size: 36px; }
  .title.small .title__shadow {
    bottom: 8px;
    font-size: 60px; }
